@import"https://fonts.googleapis.com/css?family=Libre+Baskerville:400,400i,700&display=swap";html,body{margin:0;font-family:"Libre Baskerville",serif;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:100%}body{background:#000}header{background-color:#3a3a3a;border-bottom:1px solid #333;padding:.666in .5in .1875in;grid-column-start:1;grid-column-end:3;grid-row-start:1;grid-row-end:2}header h1,header h2{color:#fff;margin:0;text-align:center}header h1{font-size:41px}header h2{margin-top:.166em;font-weight:normal;font-size:18px}h3{font-size:21px;margin-bottom:.66em}aside h3{font-size:18px;margin-bottom:.25em}a{color:#910}h4{font-size:14px;margin:0 0 .333em;line-height:100%}p,ul,span,code,tt,kbd,td{font-size:12px;line-height:1.4}ul.bulleted{padding-left:2.5em;list-style:initial}#resume-container{width:8.5in;margin:0 auto;background:#fff}@media only print{a{color:inherit;text-decoration:none}abbr,abbr[title]{border-bottom:none}body{background:initial}#resume-container{width:initial;height:initial;margin:initial}}#main{display:grid;height:100%;grid-gap:0;grid-template-columns:11fr 29fr;grid-template-rows:1.6in auto}#left aside:first-child h3,#right h3:first-child{margin-top:0}#left{padding:.25in .25in .25in 0;background-color:#eee;border-right:1.5px solid #ebebeb;grid-column-start:1;grid-row-start:2}#right{padding:.25in;grid-column-start:2;grid-row-start:2}#right section+section{margin-top:1.5em}span.sm-caps{font-size:small;font-variant:small-caps}span.fa{width:1em;margin-left:1px;text-align:center}span.in-progress{font-style:italic;color:#999}span.company-name{font-style:italic}span.location{color:#555}table{border-spacing:0 .5em;margin-top:-0.5em;margin-bottom:-0.5em}table .years{width:13%;padding-right:2%;vertical-align:top}table .years .months{font-size:11px}table .employer{font-style:italic}table .employer .location{font-style:normal}table .job-details h4{display:inline;margin:0}table .job-details h4:after{content:" ∙ "}table tr.previous{color:#555}table td{vertical-align:top}table p{margin:0}#projects>div{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:.5em}aside{text-align:right}aside ul,table ul{list-style:none;padding-left:0;margin-top:0;margin-bottom:0}aside p,section p{margin-top:0}